@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';


.game__dice-container {
  display: flex;
  max-width: 650px;
  flex-wrap: wrap;
  row-gap: 20px;
  column-gap: 20px;
  margin: 20px;

  .eureka-button__content-container {
    display: flex;
    align-items: stretch;
    justify-content: stretch;
    padding: 0;
    height: 100%;
    width: 100%;
    flex-wrap: wrap;
  }
}

.dice {
  &.frozen {
    background-color: var(--eureka-core-color-grey-06 );
  }
  padding: 0;
  background-color: var(--eureka-core-cautionary, eureka-light-theme('core-color-cautionary') );
  width: 104px;
  height: 104px;
  border-radius: 10%;
  margin-right : 10px;
}
.dot{
  display: block;
  border-radius: 50%;
  background-color:var(--eureka-core-color-white, #FFF);
}

.second-face{
  display: flex ;
  justify-content: space-between;
}

.first-face {
  display: flex;
  .dot {
    flex: 50%;
    transform: scale(0.25);
  }
}

.second-face  {
  display: flex;
  .dot {
    flex: 50%;
    transform: scale(0.5, 0.25);
  }
}

.third-face {
  display: flex;
  .dot {
    flex: 33%;
    transform: scale(0.75, 0.25);
  }
}



.fourth-face {
  flex-wrap: wrap;
  display: flex;
  .dot:nth-of-type(1), .dot:nth-of-type(2), .dot:nth-of-type(3), .dot:nth-of-type(4) {
    flex: 50%;
    transform: scale(0.5);
  }
}

.fifth-face {
  flex-wrap: wrap;
  display: flex;
  .dot:nth-of-type(1), .dot:nth-of-type(2), .dot:nth-of-type(4), .dot:nth-of-type(5) {
    flex: 33%;
    transform: scale(0.5, 0.75);
  }

  .dot:nth-of-type(3) {
    flex: 100%;
    transform: scale(0.25, 0.75);
  }


}

.sixth-face {
  flex-wrap: wrap;
  display: flex;
  .dot:nth-of-type(1), .dot:nth-of-type(2), .dot:nth-of-type(3) {
    flex: 50%;
    transform: scale(0.5, 0.75);
  }

  .dot:nth-of-type(4), .dot:nth-of-type(5), .dot:nth-of-type(6) {
    flex: 50%;
    transform: scale(0.5, 0.75);
  }


}
